<template>
  <div class="feed-selector">
    <div class="header-section">
      <h1 class="page-title">{{ $t('feed.selector.title') }}</h1>
      <p class="page-subtitle">{{ $t('feed.selector.subtitle') }}</p>
    </div>
    <p class="intro-text">{{ $t('page2.intro') }}</p>

    <div class="feed-groups">
      <div v-for="group in feedGroups" :key="group.class" class="feed-group">
        <h3>{{ getFeedLabel(group.class) }}</h3>
        <Multiselect
          v-model="selectedFeeds[group.class]"
          :options="group.feeds"
          mode="multiple"
          :searchable="true"
          :closeOnSelect="false"
          :canDeselect="true"
          :createOption="false"
          :placeholder="$t('feed.select')"
          :disabled="isGroupDisabled(group.class)"
          @change="handleSelection(group.class)"
          class="feed-multiselect"
        >
          <template v-slot:tag="{ option, remove, disabled }">
            <div class="multiselect-tag">
              {{ option }}
              <span 
                class="multiselect-tag-remove" 
                @click.prevent="remove(option)"
                v-if="!disabled"
              >
                √ó
              </span>
            </div>
          </template>
        </Multiselect>
        <div class="selection-info">
          {{ $t('feed.selected') }}: {{ (selectedFeeds[group.class] || []).length }}/{{ getMaxSelections(group.class) }}
          <span v-if="group.class === 'Kaba yem'" class="required">
            ({{ $t('feed.minimum_one') }})
          </span>
        </div>
        <div v-if="selectedFeeds[group.class]?.length" class="selected-feeds-table">
          <table>
            <thead>
              <tr>
                <th>{{ getFeedLabel(group.class) }}</th>
                <th>{{ $t('price') }}</th>
                <th>{{ $t('minimum') }}</th>
                <th>{{ $t('maximum') }}</th>
                <th>{{ $t('action') }}</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="feed in selectedFeeds[group.class]" :key="feed">
                <td>{{ feed }}</td>
                <td>
                  <input 
                    type="number" 
                    :value="getFeedValue(feed, 'price')"
                    min="0"
                    step="0.01"
                    class="table-input"
                    @input="updateFeedValue(feed, 'price', $event)"
                  >
                </td>
                <td>
                  <input 
                    type="number" 
                    :value="getFeedValue(feed, 'min')"
                    min="0"
                    step="0.1"
                    class="table-input"
                    @input="updateFeedValue(feed, 'min', $event)"
                  >
                </td>
                <td>
                  <input 
                    type="number" 
                    :value="getFeedValue(feed, 'max')"
                    min="0"
                    step="0.1"
                    class="table-input"
                    @input="updateFeedValue(feed, 'max', $event)"
                  >
                </td>
                <td>
                  <button 
                    class="delete-button"
                    @click="handleDeleteFeed(group.class, feed)"
                  >
                    √ó
                  </button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>

    <div class="validation-message" v-if="validationMessage">
      {{ validationMessage }}
    </div>

    <div class="button-group">
      <button 
        @click="handleBack" 
        class="back-button"
      >
        {{ $t('button.back') }}
      </button>
      <button 
        @click="handleCalculate" 
        class="calculate-button"
        :disabled="!isValid"
      >
        {{ $t('button.calculate') }}
      </button>
    </div>
  </div>
</template>

<script>
import { reactive } from 'vue'
import dataFeedEn from '../optionsen.js'
import Multiselect from '@vueform/multiselect'
import translations from '../translations.js'

const FEED_CLASS_MAPPING = {
  'Kaba yem': 'feed.forage',
  'Karma': 'feed.mixed',
  'Enerji': 'feed.energy',
  'Protein': 'feed.protein',
  'Diƒüer Yem': 'feed.other'
}

const MAX_SELECTIONS = {
  'Diƒüer Yem': 3,
  'Kaba yem': 6,
  'Karma': 6,
  'Enerji': 6,
  'Protein': 6
}

export default {
  name: 'FeedSelector',
  components: {
    Multiselect
  },
  props: {
    currentLanguage: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      selectedFeeds: reactive({}),
      feedValues: reactive({}),
      validationMessage: '',
      maxSelections: MAX_SELECTIONS,
      calculatorResult: this.$route.params.calculatorResult
    }
  },
  computed: {
    feedGroups() {
      const groups = {}
      dataFeedEn.forEach(item => {
        if (!groups[item.TDNClass]) {
          groups[item.TDNClass] = {
            class: item.TDNClass,
            feeds: []
          }
        }
        groups[item.TDNClass].feeds.push(item.feed)
      })
      return Object.values(groups)
    },
    isValid() {
      const kabaYemCount = (this.selectedFeeds['Kaba yem']?.length || 0)
      return kabaYemCount >= 1
    }
  },
  methods: {
    $t(key) {
      return translations[this.currentLanguage][key] || key
    },
    getFeedLabel(className) {
      return this.$t(FEED_CLASS_MAPPING[className] || className)
    },
    getFeedValue(feed, property) {
      if (!this.feedValues[feed]) {
        this.feedValues[feed] = {
          price: 1,
          min: 0,
          max: 99999
        }
      }
      return this.feedValues[feed][property]
    },
    updateFeedValue(feed, property, event) {
      const value = parseFloat(event.target.value)
      if (!this.feedValues[feed]) {
        this.feedValues[feed] = {
          price: 1,
          min: 0,
          max: 100
        }
      }
      this.feedValues[feed][property] = value
    },
    getMaxSelections(groupClass) {
      return this.maxSelections[groupClass] || 6
    },
    handleSelection(groupClass) {
      if (!this.selectedFeeds[groupClass]) {
        this.selectedFeeds[groupClass] = []
      }

      const maxForGroup = this.maxSelections[groupClass]
      if (this.selectedFeeds[groupClass].length > maxForGroup) {
        this.validationMessage = this.$t('feed.max_selections_for_category', { max: maxForGroup })
        // Reset to previous valid state by removing the last added item
        this.selectedFeeds[groupClass].pop()
        return
      }

      // Get all current feeds across all groups
      const allCurrentFeeds = Object.values(this.selectedFeeds).flat()
      
      // Remove values for deselected feeds
      Object.keys(this.feedValues).forEach(feed => {
        if (!allCurrentFeeds.includes(feed)) {
          delete this.feedValues[feed]
        }
      })

      // Initialize values for newly selected feeds
      this.selectedFeeds[groupClass].forEach(feed => {
        if (!this.feedValues[feed]) {
          this.feedValues[feed] = {
            price: 1,
            min: 0,
            max: 100
          }
        }
      })

      this.validationMessage = ''
    },
    isGroupDisabled(groupClass) {
      return this.selectedFeeds[groupClass]?.length >= this.maxSelections[groupClass]
    },
    async handleCalculate() {
      if (!this.isValid) return

      // Dummy API call
      
      const body = {"m": this.feedValues, "r": this.calculatorResult};
      console.log({body});
      try {
        const response = await fetch('http://127.0.0.1:5000/solve', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(body)
        });

        const json = await response.json();
        if (json["status"] === "optimal") {
          console.log(json["feeds"], json["values"]);
        } else {
          console.log('Error');
        }
      } catch (error) {
        console.error('Error during fetch:', error);
      }
    },
    handleBack() {
      this.$emit('go-back')
    },
    handleDeleteFeed(groupClass, feed) {
      // Remove the feed from selectedFeeds
      this.selectedFeeds[groupClass] = this.selectedFeeds[groupClass].filter(f => f !== feed)
      
      // Remove the feed values
      delete this.feedValues[feed]

      // Clear the validation message since we're removing an item
      this.validationMessage = ''

      // If no feeds are selected for this group, clean up
      if (this.selectedFeeds[groupClass].length === 0) {
        delete this.selectedFeeds[groupClass]
      }
    }
  }
}
</script>

