<template>
  <nav class="navigation">
    <div class="nav-content">
      <div class="nav-links">
        <router-link to="/" class="nav-link">{{ $t('nav.blog') }}</router-link>
        <router-link to="/calculator" class="nav-link">{{ $t('nav.calculator') }}</router-link>
      </div>
      <div class="language-selector-wrapper">
        <select v-model="$props.currentLanguage" class="language-selector" @change="handleLanguageChange">
          <option value="en">EN</option>
          <option value="tr">TR</option>
        </select>
      </div>
    </div>
  </nav>
</template>

<script>
import translations from '../translations.js'

export default {
  name: 'Navigation',
  props: {
    currentLanguage: {
      type: String,
      required: true
    }
  },
  methods: {
    $t(key) {
      return translations[this.currentLanguage][key] || key
    },
    handleLanguageChange(event) {
      this.$emit('language-change', event.target.value)
    }
  }
}
</script>

