<template>
  <div class="blog-post">
    <router-link to="/" class="back-link">‚Üê {{ $t('blog.back_to_list') }}</router-link>
    
    <div v-if="loading" class="loading">
      {{ $t('blog.loading') }}
    </div>
    
    <div v-else-if="error" class="error">
      {{ $t('blog.post_not_found') }}
    </div>
    
    <div v-else class="content" v-html="content"></div>
  </div>
</template>

<script>
import MarkdownIt from 'markdown-it'
import translations from '../translations.js'

export default {
  name: 'BlogPost',
  props: {
    currentLanguage: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      content: '',
      loading: true,
      error: false,
      md: new MarkdownIt()
    }
  },
  methods: {
    $t(key) {
      return translations[this.currentLanguage][key] || key
    },
    normalizeText(text) {
      return text
        .toLowerCase()
        .replace(/ƒü/g, 'g')
        .replace(/√º/g, 'u')
        .replace(/≈ü/g, 's')
        .replace(/ƒ±/g, 'i')
        .replace(/√∂/g, 'o')
        .replace(/√ß/g, 'c')
        .replace(/[^\w\s-]/g, '')
        .replace(/\s+/g, '-')
        .trim()
    },
    async findBlogNumber() {
      // Try all blog posts until we find a match
      for (let i = 1; i <= 4; i++) {
        try {
          const response = await fetch(`/markdowns/${this.currentLanguage}/blog${i}.md`)
          const text = await response.text()
          const firstLine = text.split('\n')[0]
          const title = firstLine.replace('# ', '')
          const normalizedTitle = this.normalizeText(title)
          
          if (normalizedTitle === this.$route.params.title) {
            return i
          }
        } catch (error) {
          console.error(`Error checking blog ${i}:`, error)
        }
      }
      throw new Error('Blog post not found')
    },
    async loadContent() {
      this.loading = true
      this.error = false
      try {
        const blogNumber = await this.findBlogNumber()
        const response = await fetch(`/markdowns/${this.currentLanguage}/blog${blogNumber}.md`)
        if (!response.ok) throw new Error('Blog post not found')
        const text = await response.text()
        this.content = this.md.render(text)
      } catch (err) {
        console.error('Error loading blog post:', err)
        this.error = true
      } finally {
        this.loading = false
      }
    }
  },
  watch: {
    '$route': 'loadContent',
    currentLanguage: 'loadContent'
  },
  mounted() {
    this.loadContent()
  }
}
</script>

<style scoped>
.blog-post {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.markdown-content {
  line-height: 1.6;
  font-size: 1.1rem;
}

.markdown-content h1,
.markdown-content h2,
.markdown-content h3 {
  margin-top: 2rem;
  margin-bottom: 1rem;
  color: #2c3e50;
}

.back-link {
  display: inline-block;
  margin-top: 2rem;
  padding: 8px 16px;
  background-color: #42b983;
  color: white;
  text-decoration: none;
  border-radius: 4px;
  transition: background-color 0.3s;
}

.back-link:hover {
  background-color: #3aa876;
}

.loading {
  text-align: center;
  padding: 2rem;
  color: #666;
}

.error {
  text-align: center;
  padding: 2rem;
  color: #e74c3c;
}

.error p {
  margin-bottom: 1rem;
}
</style> 