<template>
  <div class="blog-list">
    <div class="blog-header">
      <h1>{{ $t('blog.title') }}</h1>
      <p class="subtitle">{{ $t('blog.subtitle') }}</p>
    </div>
    
    <div class="search-container">
      <div class="search-wrapper">
        <input 
          type="text" 
          v-model="searchQuery" 
          :placeholder="$t('blog.search_placeholder')"
          class="search-input"
        >
        <span class="search-icon">üîç</span>
      </div>
    </div>

    <div v-if="loading" class="loading">
      {{ $t('blog.loading') }}
    </div>

    <div v-else-if="filteredPosts.length === 0" class="no-results">
      <div class="no-results-content">
        <span class="no-results-icon">üìö</span>
        <p>{{ $t('blog.no_results') }}</p>
      </div>
    </div>

    <div class="blog-grid">
      <router-link 
        v-for="post in filteredPosts" 
        :key="post.id"
        :to="'/blog/' + post.urlTitle"
        class="blog-card"
      >
        <div class="blog-card-content">
          <h2>{{ post.title }}</h2>
          <div class="blog-card-footer">
            <span class="read-more">{{ $t('blog.read_more') }}</span>
            <span class="arrow">‚Üí</span>
          </div>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
import translations from '../translations.js'

export default {
  name: 'BlogList',
  props: {
    currentLanguage: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      posts: [],
      searchQuery: '',
      loading: true
    }
  },
  computed: {
    filteredPosts() {
      if (!this.searchQuery) return this.posts
      const query = this.searchQuery.toLowerCase()
      return this.posts.filter(post => {
        return post.title.toLowerCase().includes(query) ||
               post.excerpt.toLowerCase().includes(query)
      })
    }
  },
  methods: {
    $t(key) {
      return translations[this.currentLanguage][key] || key
    },
    normalizeText(text) {
      return text
        .toLowerCase()
        .replace(/ƒü/g, 'g')
        .replace(/√º/g, 'u')
        .replace(/≈ü/g, 's')
        .replace(/ƒ±/g, 'i')
        .replace(/√∂/g, 'o')
        .replace(/√ß/g, 'c')
        .replace(/[^\w\s-]/g, '') // Remove punctuation
        .replace(/\s+/g, '-') // Replace spaces with hyphens
        .trim()
    },
    async loadPosts() {
      this.loading = true
      try {
        const posts = []
        
        // Load all blog posts
        for (let i = 1; i <= 4; i++) {
          try {
            const response = await fetch(`/markdowns/${this.currentLanguage}/blog${i}.md`)
            const text = await response.text()
            const firstLine = text.split('\n')[0]
            const title = firstLine.replace('# ', '')
            
            // Create URL-friendly title
            const urlTitle = this.normalizeText(title)
            
            posts.push({
              id: i,
              title: title,
              urlTitle: urlTitle,
              excerpt: this.$t(`blog.post${i}_excerpt`)
            })
          } catch (error) {
            console.error(`Error loading blog ${i}:`, error)
          }
        }
        
        this.posts = posts
      } catch (error) {
        console.error('Error loading blog posts:', error)
      } finally {
        this.loading = false
      }
    }
  },
  watch: {
    currentLanguage: 'loadPosts'
  },
  mounted() {
    this.loadPosts()
  }
}
</script>

